重新发牌3次后，视为流局。流局即消耗一局游戏数。<br/>

六、明牌规则<br/>
明牌开局：倍数x5倍<br/>
发牌时明牌：随着发到手的牌越多，明牌的倍数降低，直到17张牌都发完后，明牌按钮消失。<br/>

七、加倍规则<br/>
确定完地主后三名玩家都可选择加倍，选择加倍后自己倍数X2。<br/>

八、出牌规则<br/>
地主首先出牌，按逆时针方向跟牌。每名玩家可以选择用更大的牌跟牌或不跟牌。玩家的牌打出后无人跟牌，则该玩家继续出牌。当一名玩家打完所有手牌时，该玩家所属队伍获胜。<br/>

九、春天规则<br/>
地主获胜且农民未出一张牌称为“春天”；农民获胜且地主只出过一手牌也称为“春天”。<br/>

十、合理牌型<br/>
火箭：即双王（大王和小王），最大的牌。<br/>
炸弹：四张点数相同的牌，如：7777。<br/>
单牌：任意一张单牌。<br/>
对子：任意两张点数相同的牌。<br/>
三张：任意三张点数相同的牌。<br/>
三带一：点数相同的三张牌带一张单牌或者一对牌。<br/>
连对：两对或两对以上的连续对牌，不包括2和双王。<br/>
顺子：三张或三张以上的连续单牌，如678或910JQK。不包括2和双王。<br/>
三顺：两个或两个以上的连续三张，不包括2和双王。<br/>
飞机：三顺+同数量的单牌或对牌。<br/>
四带二：四张牌+两个单牌或一对（四带二不算炸弹）。<br/>

十一、牌型大小<br/>
1、大王＞小王＞2＞A＞K＞Q＞J＞10＞9＞8＞7＞6＞5，不分花色。<br/>
2、火箭＞炸弹＞一般牌型<br/>
3、一般牌型只有牌型且张数相同才可按牌点数比较大小。<br/>
4、顺子比较其中点数最大的牌。<br/>
5、飞机、三带一、四带二根据三张和四张相同的牌比大小，不比带出去的牌。<br/>

十二、算分方式（单选）<br/>
1、滚番：底分x倍数<br/>
2、加番：底分+倍数<br/>
（例如：地主叫3分倍数为3，翻出特殊底牌同花倍数为2，倍数变为3+2=5；打出一个炸弹为5+2=7，打出两个炸弹倍数变为5+2+2=9……以此类推。）<br/>

十三、计分规则<br/>
1、翻倍项：<br/>
火箭：倍数x2。<br/>
炸弾：倍数x2。<br/>
叫地主：分数即使倍数<br/>
抢地主：抢0/1/2/3次后，倍数对应1/2/4/8倍<br/>
春天：倍数x2。<br/>
【选项】明牌：倍数x5/4/3/2<br/>
【选项】加倍：倍数x2<br/>
【选项】抓底：单王、对子2倍；三条、同花、顺子、同花顺、双王3倍<br/>
2、分数结算：<br/>
若农民赢：底分x倍数（x其他勾选加倍项）。<br/>
若地主赢：两个农民输的分数相加。<br/>
</span>`},
