b）有人叫地主后进入抢地主环节。轮到叫地主却没有叫地主的玩家不能再抢地主。叫过地主的人可以抢一次地主，每抢一次翻一倍。最后抢地主的人做地主。<br/>
c）如果3人都不叫地主，则重新发牌。<br/>

五、关于流局<br/>
重新发牌3次后，视为流局。流局即消耗一局游戏数。<br/>

