发牌时明牌：随着发到手的牌越多，明牌的倍数降低，直到17张牌都发完后，明牌按钮消失。<br/>

七、产生癞子<br/>
发牌前，由系统随机指定某一点数的牌张作为“天赖子”，共4张。确定地主后，再由系统随机指定某一点数的牌张作为地赖子，赖子可以替代除大小王之外的任何牌张。<br/>
癞子可以替代除大小王之外的任何牌张。当癞子不顶替牌单出时，癞子为本牌。<br/>

八、加倍规则<br/>
确定完地主后三名玩家都可选择加倍，选择加倍后自己倍数X2。<br/>

九、出牌规则<br/>
地主首先出牌，按逆时针方向跟牌。每名玩家可以选择用更大的牌跟牌或不跟牌。玩家的牌打出后无人跟牌，则该玩家继续出牌。当一名玩家打完所有手牌时，该玩家所属队伍获胜。<br/>

十、春天规则<br/>
地主获胜且农民未出一张牌称为“春天”；农民获胜且地主只出过一手牌也称为“春天”。<br/>

十一、合理牌型<br/>
火箭：即王炸（大王和小王）。最大的牌。<br/>
赖炸：由纯赖子组成的炸弹，可以是天赖子和地赖子组合成。<br/>
硬炸：至少四张点数相同、没有赖子的牌。如：7777。<br/>
软炸：由赖子搭配而成的炸弹。<br/>
单牌：任意一张单牌。<br/>
对子：任意两张点数相同的牌。<br/>
三张：任意三张点数相同的牌。<br/>
三带一：点数相同的三张牌带一张单牌或者一对牌。<br/>
连对：三对或三对以上的连续对牌，不包括2和双王。<br/>
顺子：五张或五张以上的连续单牌，如56789或910JQKA。不包括2和双王。<br/>
三顺：两个或两个以上的连续三张，不包括2和双王。<br/>
飞机：三顺+同数量的单牌或对牌。<br/>
四带二：四张牌+两个单牌或一对（四带二不算炸弹）。<br/>

十二、牌型大小<br/>
1、大王＞小王＞2＞A＞K＞Q＞J＞10＞9＞8＞7＞6＞5，不分花色。<br/>
2、王炸＞赖炸＞硬炸＞软炸＞一般牌型<br/>
3、炸弹牌型同牌数的赖炸大于硬炸大于软炸，不同牌数数量多的炸弹大。<br/>
一般牌型只有牌型且张数相同才可按牌点数比较大小。<br/>
4、顺子比较其中点数最大的牌。<br/>
5、飞机、三带一、四带二根据三张和四张相同的牌比大小，不比带出去的牌。<br/>

十三、算分方式（单选）<br/>
1、滚番<br/>
倍数累计相乘<br/>
例如：抢地主倍数为3，翻出特殊底牌同花倍数为2，倍数变为6。打出1个软炸倍数变为6X2=12，打出两个软炸倍数变为6X2X2=24……以此类推。<br/>
2、加番<br/>
倍数累计相加<br/>
例如：地主叫3分倍数为3，翻出特殊底牌同花倍数为2，倍数变为3+2=5；打出一个软炸为5+2=7，打出两个炸弹倍数变为5+2+2=9……以此类推。<br/>

十四、计分规则<br/>
1、翻倍项：<br/>
火箭：倍数x5。<br/>
硬炸：倍数x4。<br/>
癞子炸：倍数x4。<br/>
五炸：倍数x4<br/>
软炸：倍数x2。<br/>
叫地主：分数即使倍数<br/>
抢地主：抢0/1/2/3次后，倍数对应1/2/4/8倍<br/>
春天：倍数x2。<br/>
【选项】明牌：倍数x5/4/3/2<br/>
【选项】加倍：农民加倍，加倍者与地主x2；地主加倍，所有人都x2。<br/>
【选项】抓底：单王、对子2倍，三条、同花、顺子、同花顺、双王3倍<br/>
2、分数结算：<br/>
若农民赢：底分x倍数（x其他勾选加倍项）。<br/>
若地主赢：两个农民输的分数相加。<br/>
</span>`},
