{'text':'皮子玩法',
'content':`<p style="font-size: 26px; color:#114B87;text-align: center;">皮子斗地主规则</p>
<span style=" display: block;color:#114B87;margin: 2% 2% 0;min-height:82%;font-size:20px;">
一、建房勾选<br/>
创建界面可选择：局数、封顶、算分方式、叫牌优先、倒拉<br/>
个性设置可选择：发牌模式、记牌器、抓底、三张带牌选项、飞机带牌选项<br/>

二、发牌规则<br/>
一副牌54张+1张皮子共55张，每人发17张牌，留4张底牌（盖住不可见）。确定地主后，底牌归地主，“闷抓”确定的地主，只有地主可以看得见底牌；“看牌”确定的地主，所有人可见底牌。<br/>

三、叫牌顺序<br/>
第一局房主优先叫分，非第一局根据玩家建房选项（随机先叫 或 胜利者先叫）。<br/>

四、确定地主<br/>
a、选择一位玩家开始叫地主流程，玩家可选择“闷抓”、“看牌”<br/>
i、选择闷抓的玩家，确定为地主，其后的玩家可以依次选择倒、不倒<br/>
ii、选择看牌的玩家，其次还可以选择抓、不抓，选择不抓，则放弃了叫地主的权利，同时放弃了倒的权利<br/>
iii、选择抓的玩家，确认为地主，其后的玩家可以选择倒、不倒，当至少有一个玩家选择了倒，地主可以选择拉。<br/>
b、看牌后选择不抓后，逆时针轮到下一个玩家选择闷抓或看牌，前两个玩家如果不抓地主，则第三个玩家自动成为地主，效果等同闷抓，但其他两名玩家不可倒。<br/>
必抓牌<br/>
看牌情况下手牌中有以下牌型则其必须“抓”。<br/>
连炸、重炸、王炸、4个2、2个2和1个王、三个炸弹<br/>
注：以上牌型包含皮子替牌后的牌组<br/>

五、关于流局<br/>
重新发牌3次后，视为流局。流局即消耗一局游戏数。<br/>

六、倒拉规则<br/>
选出地主后农民可以主动选择‘倒’地主，这时该农民倍数X2，当地主被倒后，可以选择‘拉’该农民，此时双方倍数X4。<br/>
看牌情况下手中中有“连炸、重炸、双王、四个2”牌型则其必须倒<br/>

七、出牌规则<br/>
地主首先出牌，按逆时针方向跟牌。每名玩家可以选择用更大的牌跟牌或不跟牌。玩家的牌打出后无人跟牌，则该玩家继续出牌。当一名玩家打完所有手牌时，该玩家所属队伍获胜。<br/>

八、春天规则<br/>
地主获胜且农民未出一张牌称为“春天”；农民获胜且地主只出过一手牌也称为“春天”。<br/>

九、皮子<br/>
皮子可作为包括王在内的任何牌。<br/>

十、合理牌型<br/>
连炸：点数相连的炸弹<br/>
重炸：双王+皮子或者四张点数相同的牌+皮子组成的牌型<br/>
王炸：即双王（大王和小王），最大的牌。<br/>
炸弹：四张点数相同的牌，如：7777。<br/>
单牌：任意一张单牌。<br/>
对子：任意两张点数相同的牌。<br/>
三张：任意三张点数相同的牌。<br/>
三带一：点数相同的三张牌带一张单牌，不允许对牌。<br/>
连对：三对或三对以上的连续对牌，不包括2和双王。<br/>
顺子：五张或五张以上的连续单牌，如56789或910JQKA。不包括2和双王。<br/>
三顺：两个或两个以上的连续三张，不包括2和双王。<br/>
飞机：三顺加同数量的单牌。<br/>
四带二：四张牌+两个单牌或一对（四带二不算炸弹）。<br/>

十一、牌型大小<br/>
1、大王＞小王＞2＞A＞K＞Q＞J＞10＞9＞8＞7＞6＞5，不分花色。<br/>
2、连炸＞重炸＞王炸＞炸弹＞一般牌型<br/>
3、一般牌型只有牌型且张数相同才可按牌点数比较大小。<br/>
4、顺子比较其中点数最大的牌。<br/>
5、飞机、三带一、四带二根据三张和四张相同的牌比大小，不比带出去的牌。<br/>

十二、算分方式（单选）<br/>
1、滚番<br/>
倍数累计相乘<br/>
底分：1分<br/>
倒、拉、炸弹、春天：底分X2<br/>
重炸、反春：底分X4<br/>
连炸：底分X8<br/>
2、加番<br/>
番数累计相加<br/>
底分：1分<br/>
倒、拉、炸弹、春天：底分+1<br/>
重炸、反春：底分+2<br/>
连炸：底分+3<br/>

十三、计分规则<br/>
分数结算<br/>
若农民赢：底分x倍数（x其他勾选加倍项）。<br/>
若地主赢：两个农民输的分数相加。<br/>
</span>`}
],
iSee:false,
index:0,

}
},
methods:{
actived: function(index){
this.iSee = index;
this.index = index;
}
}
}




